import { Directive, Input, OnInit } from '@angular/core';

import { PresentationLoan } from '@backbase/data-ang/loans';

@Directive()
// eslint-disable-next-line @angular-eslint/directive-class-suffix
export abstract class LoanConfigBaseComponent<Loan extends PresentationLoan, DataConfig> implements OnInit {
  /**
   * Loan item
   */
  @Input() loan!: Loan;
  /**
   * Data based configuration
   */
  @Input() dataConfig!: DataConfig;

  ngOnInit(): void {
    if (!this.loan) {
      throw new Error(this.getErrorMessage('loan'));
    }

    if (!this.dataConfig) {
      throw new Error(this.getErrorMessage('dataConfig'));
    }
  }

  private getErrorMessage(fieldName: keyof LoanConfigBaseComponent<Loan, DataConfig>): string {
    return `[bb-loans]: "${fieldName}" input is required in "${this.constructor.name}"`;
  }
}
